.translate {
  background-color: rgba(34, 34, 34, 0.4);
  padding: 30px;
  text-align: center; }
  .translate ul {
    list-style: none;
    padding: 0;
    margin: 0; }
    .translate ul li {
      height: 30px;
      color: #FFD600;
      font-size: 20px; }
  @media (max-width: 700px) {
    .translate ul li {
      font-size: 16px; } }



